<template>
    <div>
      <h2>Solve this game to find out more about me</h2>
      <p>Use addition, subtraction, multiplication, and/or division
      to get 24 from the 4 random cards.
      <br>
      Sometimes it's solvable, sometimes it's not ......
      Reshuffle the numbers to try your luck!</p>
      <b-row>
        <b-col sm="2"
              v-for="card in [0, 1, 2, 3]"
              :key="card">
        <card @click.native="gameAddNumber(card);"
              v-model="deck[card]"></card>
        </b-col>
      </b-row>
      <br>
      <b-row>
        <b-col sm="1"
        v-for="symbol in symbols"
                :key="symbol">
      <b-button
                @click="gameAddSymbol(symbol);"
                variant="outline-secondary"
                size="lg"
                > {{symbol}} </b-button>
        </b-col>
      </b-row>
      <br>
      <b-button @click="gameReset">clear equation</b-button>
      <b-button @click="gameInit">reshuffle</b-button>
      <b-button @click="gameSubmit"> submit</b-button>
      <h2>{{ equation_string }}</h2>
    </div>

</template>
<script>
export default {
  props: ['value'],
  data() {
    return {
      symbols: ['+', '-', '*', '/', '(', ')'],
      deck: [],
      equation: [],
      equation_string: '',
      state: '',
      count: 0,
    };
  },
  created() {
    this.gameInit();
  },
  methods: {
    getRandomInt(max) {
      // return a random integer in 1-max
      return Math.floor(Math.random() * Math.floor(max));
    },
    getRandomCard() {
      // return a random card number in 1-13
      return this.getRandomInt(13) + 1;
    },
    getRandomSuit() {
      // return a random suit number in 1-4
      return this.getRandomInt(4);
    },
    gameInit() {
      // eslint-disable-next-line no-console
      console.log('gameInit');

      // initialize cards
      this.deck = [];
      for (let step = 0; step < 4; step += 1) {
        this.deck.push({
          number: this.getRandomCard(),
          suit: this.getRandomSuit(),
          state: true,
        });
        // eslint-disable-next-line no-console
        console.log(this.deck);
      }
      // reset equation and state
      this.equation = [];
      this.equation_string = '';
      this.state = 'solving';
    },
    gameReset() {
      // reset equation to empty
      this.equation_string = '';
      this.equation = [];

      // reset all cards to available
      for (let step = 0; step < 4; step += 1) {
        this.deck[step].state = true;
      }
    },
    gameAddSymbol(symbol) {
      // add a symbol to equation
      this.equation.push(symbol);
      this.equation_string = this.equation.join('');
    },
    gameDeleteSymbol() {
      // remove last symbol from equation
      this.equation.pop();
      this.equation_string = this.equation.join('');
    },
    gameAddNumber(index) {
      // add a number to equation
      if (this.deck[index].state === true) {
        this.equation.push(this.deck[index].number);
        this.equation_string = this.equation.join('');
        this.deck[index].state = false;
      }
    },
    gameSubmit() {
      // check if all cards are used
      for (let index = 0; index < 4; index += 1) {
        if (this.deck[index].state === true) {
          this.state = 'available-error';
          return;
        }
      }
      /* eslint-disable */
      try {
        if (eval(this.equation_string) === 24) {
          this.value.solved = true;
          this.state = 'solved';
          this.gameInit();
          return
        } else {
          this.state = 'calculation-error';
        }
      } catch (error) {
        this.state = 'syntatic-error';
      }
      console.log(this.state);
      /* eslint-enable */

      this.count += 1;
      if (this.count >= 6) {
        this.state = 'nice-try';
      }
    },
  },
};
</script>
